import classes from './Aside.module.scss';
import cls from './ListItem/ListItem.module.scss';
import ListItem from './ListItem/ListItem'

const Aside = (props) => {
  return (
    <aside className={classes.aside}>
      <nav>
        <ul>
          <ListItem src="profile" text="Profile" />
          <ListItem src="dialogs" text="Messages" />
          <ListItem src="friends" text="Friends" />
          <ListItem src="users" text="Find Friends" />
          <ListItem src="settings" text="Settings" />
          <ListItem src="exit" text="Exit" />
        </ul>
      </nav>
    </aside>
  )
}

export default Aside;