import classes from './User.module.scss';
import userPhoto from '../../../../img/user-photo.png';
import DataItem from './DataItem/DataItem';
import Preloader from '../../../common/Preloader/Preloader';
import ProfileStatusHooks from './ProfileStatus/ProfileStatusHooks';

const User = (props) => {


  if (!props.profile) {
    return <Preloader />
  } else {
    return (
      <div className={classes.user}>
        <div className={classes.user__wrapper}>
          <div className={classes.user__photo}>
            <img src={props.profile.photos.small == null ? userPhoto : props.profile.photos.small} />
          </div>
          <div className={classes.user__data}>
            <ul className={classes.list}>
              <DataItem label="Name:" value={props.profile.fullName} />
              <DataItem label="Twitter:" value={props.profile.contacts.twitter} />
              <DataItem label="VK:" value={props.profile.contacts.vk} />
              <ProfileStatusHooks status={props.status} updateStatus={props.updateStatus} />
            </ul>
          </div>
        </div>
      </div>
    )
  }
};
export default User;