import React from 'react';
import { connect } from "react-redux";
import Profile from './Profile';
import { getProfileThunkCreator, getStatusThunkCreator, updateStatusThunkCreator } from '../../../redux/profile-reducer';
import { WidthAuthRedirectComonent } from '../../../hoc/AuthRedirect';
import { compose } from 'redux';
import { withRouter } from '../../../hoc/WithRouter';

class ProfileContainer extends React.Component {

  componentDidMount() {
    let userId = this.props.match ? this.props.match.params.userId : this.props.userId;
    if (!userId) {

    }
    this.props.getProfileThunkCreator(userId)
    this.props.getStatusThunkCreator(userId)
  }

  render() {
    return (
      <Profile {...this.props} profile={this.props.profile} status={this.props.status} updateStatus={this.props.updateStatusThunkCreator} />
    )
  }
}

const mapStateToProps = (state) => (
  {
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    userId: state.auth.id,
    isAuth: state.auth.isAuth
  })

const objectAC = {
  getProfileThunkCreator,
  getStatusThunkCreator,
  updateStatusThunkCreator
}

const ProfileConnect = compose(
  connect(mapStateToProps, objectAC),
  withRouter,
  WidthAuthRedirectComonent
)(ProfileContainer);

export default ProfileConnect;