
import { Field, reduxForm } from 'redux-form';
import { maxLengthCreator, requiredFiled } from '../../utils/validators/validators';
import { Input } from '../common/FormsCntrols/FromsControls';
import classes from './Login.module.scss';

const maxL30 = maxLengthCreator(30)

const LoginForm = (props) => {

  return (
    <form onSubmit={props.handleSubmit}>
      <div className={classes.input_item}>
        <Field component={Input} name={'Login'} className='input' type={"text"} placeholder={"Login"} validate={[requiredFiled, maxL30]} />
      </div>
      <div className={classes.input_item}>
        <Field component={Input} name={'Password'} className='input' type={"password"} placeholder={"password"} validate={[requiredFiled, maxL30]} />
      </div>
      <div className={`${classes.input_item} ${classes.checkbox}`}>
        <Field component={Input} name={'remember'} id='remember' className={classes.checkbox_input} type={"checkbox"} placeholder={"password"} />
        <label htmlFor="remember">Remember Me</label>
      </div>
      {props.error && <div className={classes.error_field}>{props.error}</div>}

      <div className={classes.input_item}>
        <button className='btn' type={"submit"}  >Login</button>
      </div>
    </form>
  )
}

const ReduxLoginForm = reduxForm({
  form: 'login'
})(LoginForm)

export default ReduxLoginForm