import React from 'react'
import InputBtn from '../../Profile/Posts/InputBtn/InputBtn'
import IndividualDialogs from '../IndividualDialogs/IndividualDialogs'
import ReduxMessageForm from './Message/FormMessage'
import classes from './Messages.module.scss'



const Messages = (props) => {
  const inputEl = React.createRef()

  // const setMessage = () => {
  //   props.setMessage(props.messageVal)
  // }
  // const updateInputFiled = () => {
  //   const messageEl = inputEl.current.value
  //   props.updateInputFiled(messageEl)
  // }

  const onSubmit = (values) => {
    props.setMessage(values.message)
  }

  return (
    <div className={classes.body}>
      <div className={classes.filed}>
        <IndividualDialogs messagesData={props.messages} />
      </div>
      {/* <div className={classes.writeMessage}>
        <InputBtn textBtn="Send" func={() => {
          setMessage()
        }}
          changeFunc={() => {
            updateInputFiled()
          }}
          val={props.messageVal}
          refSrc={inputEl} />
      </div> */}

      <ReduxMessageForm onSubmit={onSubmit} textBtn="Send" />
    </div>
  )
}
export default Messages