import classes from './Users.module.scss';
import Pagination from '../../Pagination/Pagination';
import User from './User';

const Users = (props) => {
  return (
    <div className={classes.users}>
      {props.usersData.usersData.map(user => {
        return (
          <User key={user.id} user={user} usersData={props.usersData} />
        )
      })}
      <Pagination totalItemsCount={props.usersData.totalUsersCount} pageSize={props.usersData.pageSize} currentPage={props.usersData.currentPage} func={props.pageChanged} />
    </div >
  )
}

export default Users