import { authThunkCreator } from "./auth-reducer";

const SET_INIT = "SET_INIT";

let inititalState = {
  initialazed: false
}

const appReducer = (state = inititalState, action) => {
  switch (action.type) {
    case SET_INIT:
      return {
        ...state,
        initialazed: true
      }
    default:
      return state
  }
}

const setInitSuccess = () => {
  return (
    {
      type: SET_INIT,
    }
  )
}

export const initialize = () => (dispatch) => {
  let result = dispatch(authThunkCreator());

  // если много dispatch (они должны что-то вернуть)
  Promise.all([result]).then(() => {
    dispatch(setInitSuccess())
  })

  // result.then(() => {
  //   dispatch(setInitSuccess())
  // })
}

export default appReducer