import classes from './FromsControls.module.scss'

const FormControl = ({ input, meta, ...props }) => {
  return (
    <div className={meta.touched && !meta.valid ? `${classes.field} ${classes.error}` : classes.field} >
      {/* <input className={classes.input} {...input} {...props} /> */}
      {props.child}
      {meta.touched && !meta.valid ? <span className={classes.error_text}>{meta.error}</span> : null}
    </div>
  )
}
