const SEND_MESSAGE = "SEND_MESSAGE"

const inititalState = {
  dialogsData: [
    { id: 1, name: "Max Fax", },
    { id: 2, name: "Vika", },
    { id: 3, name: "Ivan", },
    { id: 4, name: "Petr", },
  ],

  messagesData: [
    {
      id: '1',
      message: 'Hi',
      fromClass: 'toMe'
    },
    {
      id: '2',
      message: 'Hello',
      fromClass: 'fromMe'
    }, {
      id: '3',
      message: 'How are you?',
      fromClass: 'toMe'
    },
  ],
}

const messagesReducer = (state = inititalState, action) => {
  switch (action.type) {

    case SEND_MESSAGE:

      return {
        ...state,
        messagesData: [...state.messagesData, action.newMessage]
      }
    // вместо push для добавления элемента в массив используеться spread оператор
    // stateCopy.messagesData.push(action.newMessage)
    // stateCopy.messageVal = ''

    default:
      return state;
  }
}

export const sendMessageActionCreator = (messageText) => {
  return {
    type: SEND_MESSAGE,
    newMessage: messageText,
  }
}

export default messagesReducer