import React, { useEffect, useState } from "react";
import classes from "./ProfileStatus.module.scss";

const ProfileStatusHooks = (props) => {
  const [editMode, activateEditMode] = useState(false)
  const [status, onStatusChange] = useState(props.status)

  const deactivateEditMode = () => {
    activateEditMode(false)
    props.updateStatus(status)
  }

  useEffect(() => {
    onStatusChange(props.status)
  }, [props.status])

  return (
    <>
      {!editMode ?
        <div className={classes.status}>
          <span onDoubleClick={() => { activateEditMode(editMode ? false : true) }}>{props.status === null || props.status === "" || props.status === " " ? "Create status" : props.status}</span>
        </div> :
        <div className={classes.status}>
          <input onChange={(e) => { onStatusChange(e.currentTarget.value) }} autoFocus={true} onBlur={() => { deactivateEditMode() }} type="text" value={status} />
        </div>
      }
    </>
  )
}
export default ProfileStatusHooks