import classes from './Pagination.module.scss';
const PaginationElem = (props) => {
  return (
    <li className={classes.pagination__elem} >
      <button onClick={() => { props.func(props.num) }} className={`${classes.pagination__btn} ${props.active} btn`}>{props.num}</button>
    </li >
  )
}
export default PaginationElem 