import { connect } from 'react-redux';
import { sendMessageActionCreator } from '../../../redux/messages-reducer';
import Dialogs from './Dialogs';
import { WidthAuthRedirectComonent } from '../../../hoc/AuthRedirect';
import { compose } from 'redux';

const mapStateToProps = (state) => {
  return {
    dialogs: state.messagesPage.dialogsData,
    messages: state.messagesPage.messagesData,
    messageVal: state.messagesPage.messageVal,
    messagesLength: state.messagesPage.dialogsData.length,
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    setMessage(text) {
      let newMessage = {
        id: 5,
        message: text,
        fromClass: 'fromMe'
      }
      dispatch(sendMessageActionCreator(newMessage))
    },
  }
}

const DialogsContainer = compose(
  connect(mapStateToProps, mapDispatchToProps),
  WidthAuthRedirectComonent,
)(Dialogs);

export default DialogsContainer
