// import React from 'react';
import { connect } from 'react-redux';
import { addPostActionCreator } from '../../../../redux/profile-reducer';
import Posts from './Posts';

//В первой функции должны лежать данные из state 
let mapStateToProps = (state) => {
  return {
    postsData: state.profilePage.postsData,
    valInput: state.profilePage.newPostVal,
    // postsLength: state.profilePage.postsData.length
  }
}

// Во второй функции передаем callbacks
let mapDispatchToProps = (dispatch) => {
  return {
    addPost(text) {
      let newPost = {
        // id: Number(mapStateToProps().postsLength + 1),
        id: '5',
        postText: text,
        btnText: "Like"
      }
      dispatch(addPostActionCreator(newPost))
    },
  }
}

const PostContainer = connect(mapStateToProps, mapDispatchToProps)(Posts)

export default PostContainer;