import classes from './Users.module.scss';
import img from './../../../img/user-photo.png';
import { NavLink } from 'react-router-dom';

const User = (props) => {
  return (
    <div key={props.user.id} className={classes.user}>
      <div className={classes.user__left}>
        <div className={classes.user__photo}>
          <NavLink to={`/profile/${props.user.id}`}>
            <img src={props.user.photos.large == null ? img : props.user.photos.large} alt="#" />
          </NavLink>
        </div>
        {props.user.followed == true ?
          <button disabled={props.usersData.progressFollowing.some(id => id === props.user.id)} onClick={() => {
            props.usersData.unfollowing(props.user.id)
          }} className={`btn ${classes.user__btn}`}>
            Unfollow
          </button>
          :
          <button disabled={props.usersData.progressFollowing.some(id => id === props.user.id)} onClick={() => {
            props.usersData.following(props.user.id)
          }} className={`btn ${classes.user__btn}`}>
            Follow
          </button>
        }
      </div>
      <div className={classes.user__right}>
        <div className={classes.user__top}>
          <div className={classes.user__name}>{props.user.name}</div>
          <div className={classes.user__location}>
            <span>"user.location.country"</span>
            <span>"user.location.city"</span>
          </div>
        </div>
        <div className={classes.user__status}>
          {props.user.status}
        </div>
      </div>
    </div >
  )
}

export default User