import { connect } from "react-redux";
import { getUsersThunkCreator, unfollowThunkCreator, followThunkCreator, changePageAC } from "../../../redux/users-reducer";
import React from 'react';
import Users from "./Users";
import Preloader from "../../common/Preloader/Preloader";
import { getUserReselector, getpageSize, getTotalUsersCount, getcurrentPage, getLoading, getprogressFollowing } from "../../../redux/selectors/usersSelectors";

class UsersAPIComponent extends React.Component {

  //Если мы хотим передать только пропсы, то данный конструктор можно не писать, всё возьмётся а автоматом из React.Component 
  constructor(props) {
    super(props)
  }

  componentDidMount = () => {
    this.props.getUsers(this.props.currentPage, this.props.pageSize)
  }

  pageChanged = (pageNum) => {
    this.props.getUsers(pageNum, this.props.pageSize)
    this.props.changePageAC(pageNum)
  }

  render = () => {
    return (
      <>
        {this.props.isLoading === true ? <Preloader /> : null}
        <Users usersData={this.props} pageChanged={this.pageChanged} />
      </>
    )
  }
}


const mapStateToProps = (state) => {
  return {
    usersData: getUserReselector(state),
    pageSize: getpageSize(state),
    totalUsersCount: getTotalUsersCount(state),
    currentPage: getcurrentPage(state),
    isLoading: getLoading(state),
    progressFollowing: getprogressFollowing(state),
  }
}
const objectAC = {
  getUsers: getUsersThunkCreator,
  unfollowing: unfollowThunkCreator,
  following: followThunkCreator,
  changePageAC
}

// Вместо того, чтобы передавать mapDispatchToProps, мы можем передать объект, который ссылаеться на ActionCreators

const UsersContainer = connect(mapStateToProps, objectAC)(UsersAPIComponent)

export default UsersContainer