import { useState } from 'react';
import classes from './Pagination.module.scss';
import PaginationElem from './PaginationElem';

const Pagination = ({ portionSize = 10, ...props }) => {
  let pagesCount = Math.ceil(props.totalItemsCount / props.pageSize)

  let pages = [];
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i)
  }

  let portionCounts = Math.ceil(pagesCount / portionSize)
  const [portionNum, serPortionNum] = useState(1)
  let leftPortionNum = (portionNum - 1) * portionSize + 1
  let rightPortionNum = portionNum * portionSize

  return (
    <div className={classes.pagination}>
      {portionNum > 1 ? <button className='btn' onClick={() => { serPortionNum(portionNum - 1) }}>Prev</button> : null}
      <ul className={classes.pagination__list}>
        {pages
          .filter(page => page >= leftPortionNum && page <= rightPortionNum)
          .map(i => {
            return <PaginationElem key={i} active={props.currentPage === i ? classes.active : ""} num={i} func={props.func} />
          })}
      </ul>
      {portionCounts > portionNum ? <button className='btn' onClick={() => { serPortionNum(portionNum + 1) }}>Next</button> : null}
    </div>
  )
}

export default Pagination;