import { NavLink } from 'react-router-dom';
import classes from './UserDialog.module.scss';

const UserDialog = (props) => {
  return (
    <li className={`${classes.item} ${props.activeClass}`}>
      <NavLink to={`/dialogs/${props.id}`} className={navData => navData.isActive ? classes.active : null}>{props.name}</NavLink>
    </li>
  )
}

export default UserDialog; 