import classes from '../InputBtn/InputBtn.module.scss'
import { Field, reduxForm } from 'redux-form';
import { requiredFiled, maxLengthCreator } from '../../../../../utils/validators/validators';
import { Input } from '../../../../common/FormsCntrols/FromsControls';

let maxL10 = maxLengthCreator(10)

const PostForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <div className={classes.input__block}>
        <Field name={'Post'} error="Required" component={Input} type='text' className={classes.input} placeholder={'Enter the post'} validate={[requiredFiled, maxL10]} />
        <button className={`btn ${classes.btn}`} type={"submit"}>{props.textBtn}</button>
      </div>
    </form>
  )
}

const ReduxPostForm = reduxForm({
  form: 'PostForm'
})(PostForm)

export default ReduxPostForm