import classes from './Friend.module.scss'

const Friend = (props) => {
  return (
    <li className={classes.friend}>
      <div className={classes.friend__photo}>
        <img src={props.photoSrc}></img>
      </div>
      <div className={classes.friend_name}>
        {props.nameFriend}
      </div>
    </li>
  )
}
export default Friend