import classes from './Posts.module.scss';
import Post from './Post/Post';
import React from 'react';
import ReduxPostForm from './Post/PostForm';

const Posts = (props) => {

  const onSubmit = (formData) => {
    props.addPost(formData.Post)
  }

  const postsElement = props.postsData.map(({ id, postText, btnText }) => {
    return (
      <Post key={id} text={postText} btn_text={btnText} />
    )
  })
  return (
    <div className={classes.posts}>
      <div className={classes.pasts__form}>
        <ReduxPostForm onSubmit={onSubmit} textBtn="Send" />
        <div className={classes.posts__body}>
          {postsElement}
        </div>
      </div>
    </div>
  )
};

export default Posts;