import Message from '../Messages/Message/Message';
import classes from './IndividualDialogs.module.scss'
import cls from '../Messages/Message/Message.module.scss'


const IndividualDialogs = (props) => {

  const messageElements = props.messagesData.map(({ id, message, fromClass }) => {
    return <Message key={id} textMessage={message} from={cls[fromClass]} />
  })

  return (
    <div className={classes.dialogs}>
      {messageElements}
    </div>
  )
}
export default IndividualDialogs;