import { applyMiddleware, combineReducers, createStore, compose } from "redux";
import thunkMiddlewear from 'redux-thunk'
import authReducer from "./auth-reducer";
import friendsReducer from "./friends-reducer";
import messagesReducer from "./messages-reducer";
import profileReducer from "./profile-reducer";
import usersReducer from "./users-reducer";
import { reducer as formReducer } from 'redux-form';
import appReducer from "./app-reducer";

let reducerPackeges = combineReducers({
  profilePage: profileReducer,
  messagesPage: messagesReducer,
  friendPage: friendsReducer,
  usersPage: usersReducer,
  auth: authReducer,
  form: formReducer,
  app: appReducer,
});


// для расширения redux in browser
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducerPackeges, composeEnhancers(applyMiddleware(thunkMiddlewear)));

// чтобы Store умел работать с thunk, нам необходимо в createStore добавить  доп.параметр, который сможет обрабатывать thunk и возвращать объект, а не функцию с помощью applyMiddleware

// let store = createStore(reducerPackeges, applyMiddleware(thunkMiddlewear));

window.store = store;

export default store;