
import classes from './Dialogs.module.scss';
import Messages from './Messages/Messages';
import UserDialog from './UserDialog/UserDialog';

const Dialogs = (props) => {
  const userDialogs = props.dialogs.map(({ id, name }) => {
    return (
      <UserDialog key={id} id={id} name={name} />
    )
  })

  return (
    <div className={classes.dialogs}>
      <div className={classes.dialogs__wrapper}>
        <div className={classes.users__dialogs}>
          <ul className={classes.list}>
            {userDialogs}
          </ul>
        </div>
        <div className={classes.dialogs__body}>
          <Messages setMessage={props.setMessage} updateInputFiled={props.updateInputFiled} messages={props.messages} messageVal={props.messageVal} />
        </div>
      </div>
    </div>
  )
}


export default Dialogs;