export const requiredFiled = (value) => {
  if (value) {
    return undefined
  } else {
    return 'error message'
  }
}

export const maxLengthCreator = (maxLength) => (value) => {
  if (value && value.length > maxLength) {
    return `Max length ${maxLength}`
  } else {
    return undefined
  }
}

