import { stopSubmit } from "redux-form";
import { AuthAPI } from "../API/api";

const SET_USER = "SET_USER";

let inititalState = {
  "id": null,
  "login": null,
  "email": null,
  "isAuth": false,
  "isLoadin": false,
}

const authReducer = (state = inititalState, action) => {
  switch (action.type) {
    case SET_USER:
      return {
        ...state,
        ...action.data,
      }
    default:
      return state
  }
}

export const setUserData = (id, login, email, isAuth) => {
  return (
    {
      type: SET_USER,
      data: {
        id,
        email,
        login,
        isAuth
      }
    }
  )
}

// Вместо then мы можем использовать функцию async/await

export const authThunkCreator = () => async (dispatch) => {
  let response = await AuthAPI.getAuth()
  let { id, login, email } = response.data
  if (response.resultCode === 0) {
    dispatch(setUserData(id, login, email, true))
  } else if (response.resultCode === 1) {
    dispatch(setUserData(id, login, email, false))
  }
}

