import classes from '../../../Profile/Posts/InputBtn/InputBtn.module.scss'
import { Field, reduxForm } from 'redux-form';
import { Input, Textarea } from '../../../../common/FormsCntrols/FromsControls';
import { maxLengthCreator, requiredFiled } from '../../../../../utils/validators/validators';

const maxL30 = maxLengthCreator(30)

const MessageForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <div className={classes.input__block}>
        <Field name={'message'} component={Input} type='text' className={classes.input} placeholder={'Enter the message'} validate={[requiredFiled, maxL30]} />
        <button className={`btn ${classes.btn}`} type={"submit"}>{props.textBtn}</button>
      </div>
    </form>
  )
}

const ReduxMessageForm = reduxForm({
  form: 'messageForm'
})(MessageForm)

export default ReduxMessageForm